<script>
import axios from 'axios';

export default {
  data() {
    return {
      productAmount: 1,
      productData: null,
      loading: true,
      erLoading: false,
      productAddet: -1,
    };
  },
  computed: {
    product() {
      return this.productData ? this.productData : [];
    },
    category() {
      return this.productData ? this.productData.category : [];
    },
  },
  methods:
  {
    productAddetF() {
      // eslint-disable-next-line max-len
      this.productAddet = this.$store.state.cartProductsData.findIndex((element) => element.product.id === Number(this.$route.params.id));
    },
    ProductData() {
      this.erLoading = false;
      this.loading = true;
      axios.get(`${this.$store.state.URL__DATA}/api/products/${this.$route.params.id}`, {
        params: {
          page: this.page,
          limit: this.productsPerPage,
          categoryId: this.filterCategoryId,
          minPrice: this.filterPriceFrom,
          maxPrice: this.filterPriceTo,
        },
      })
        // eslint-disable-next-line no-return-assign
        .then((response) => this.productData = response.data)
        // eslint-disable-next-line no-return-assign
        .catch(() => this.erLoading = true)
        // eslint-disable-next-line no-return-assign
        .then(() => this.loading = false);
    },
    addToCard() {
      this.$store.dispatch(
        'addProductBask',
        {
          id: this.product.id,
          amount: this.productAmount,
        },
      );
      this.productAddet = this.product.id;
    },
  },
  watch: {
    '$route.params.id': {
      handler() {
        this.ProductData();
        this.productAddetF();
      },
      immediate: true,
    },
  },
};
</script>
